
import React, {useRef} from 'react';
import { Container } from 'reactstrap';
import pic from '../../assests/images/pic.png';
import './header.css'

const navLinks = [
    {
        display: 'Home',
        url: '#'
    },
    {
        display: 'About',
        url: '#'
    },
    {
        display: 'Services',
        url: '#'
    },
    {
        display: 'Media',
        url: '#'
    },
    {
        display: 'News',
        url: '#'
    },
    {
        display: 'Contact Us',
        url: '#'
    },
    {
        display: 'Certificate   ',
        url: '#'
    },
]

const Header = () => {

     const menuRef = useRef()

     const menuToggle  = ()=> menuRef.current.classList.toggle
     ('active__menu')


    return <header className="header">
        <Container>
            <div className="navigation d-flex align-items-center
      justify-content-between">
                <div className="logo">
                    {/* <h2 className='d-flex align-items-center'>
                        <i class="ri-pantone-fill"></i>SAYLANI */}
                        {/* <img src="./pic.png"/> */}

                    {/* </h2> */}
                    <div className="saylani__pic">
                    <img src={pic} alt='' className='w-00' />
                 </div>
                </div>

                <div className="nav d-flex align-items-center gap-4">
                    <div className="nav__menu" ref={menuRef} onClick={menuToggle} >
                        <ul className="nav__list">


                            {
                                navLinks.map((item, index) => (
                                    <li key={index} className="nav__item">
                                        <a href={item.url}>{item.display}</a>
                                    </li>
                                ))
                            }

                        </ul>
                    </div>

                    <div className="nav__right">
                        <p className='mb-0 d-fex align-items-center gap-2'>
                            <i class="ri-phone-fill"></i>+92 03010209887
                        </p>
                    </div>
                </div>
                <div className="mobile__menu">
                    <span>
                    <i class="ri-menu-line" onClick={menuToggle}></i>
                    </span>
                </div>
            </div>
        </Container>
    </header>;

};

export default Header;

