

import React from 'react'
import { Container, Row,Col } from 'reactstrap';
import heroImg from '../../assests/images/say.png'
import './hero-section.css'

const HeroSection = () => {
    return (
         <section>
        <Container>
            <Row>
                <Col lg='6' md='6'>
                    <div className="hero__content">
                    <h2 className='mb-4'>Donate us to support <h3 className='flood'>flood-affected</h3> families in Pakistan </h2>
                    <p>
                    Saylani Welfare is on the ground and already working with
                     local communities to assess how best to support
                      affected families, who urgently need food, Ration,
                       shelter, bedding, Medical Facility and hygiene items.

                    </p>
                    <div className="search">
                        <input type='text' placeholder="search" />
                        <button className='btn22'>Search</button>
                    </div>
                    </div>
                </Col>

                <Col lg='6' md='6' >
               
                    <img src={heroImg} alt='' className='w-100' />
                </Col>
                
            </Row>
        </Container>
    </section>
    );
};

export default HeroSection;