import React from 'react'
import './about.css'
import { Container,Row,Col } from 'reactstrap'
import aboutImg from '../../assests/images/about-us.png'
import CountUp from 'react-countup';
import './about.css';

const AboutUS = () => {
  return <section>
    <Container>
        <Row>
            <Col lg='6' md='6'>
                 <div className="about__img">
                    <img src={aboutImg} alt='' className='w-00' />
                 </div>
            </Col>

            <Col lg="6" md="6">
              <div className='about__content'>
                <h2 >About Us</h2>
                <p>Saylani Welfare is on the ground and already working with
                     local communities to assess how best to support
                      affected families, who urgently need food, Ration,
                       shelter, bedding, Medical Facility and hygiene items.

                </p>
                    <div className="about__counter">
                        <div className='d-flex gap-5 align-items-center'>
                        <div className="single__counter">
                            <span className="counter">
                                <CountUp start={0} end={200} duration={2}
                                suffix="K" />
                            </span>

                            <p className='counter__title'>
                                Food (Daily)
                            </p>
                        </div>

                        <div className="single__counter">
                            <span className="counter">
                                <CountUp start={0} end={12} duration={2}
                                suffix="k" />
                            </span>

                            <p className='counter__title'>
                                Family Adoption(Monthly)
                            </p>
                        </div>
                        </div>
                        <div className='d-flex gap-5 align-items-center'>
                        <div className="single__counter">
                            <span className="counter">
                                <CountUp start={0} end={25} duration={2}
                                suffix="K" />
                            </span>

                            <p className='counter__title'>
                                Education (Monthly)
                            </p>
                        </div>

                        <div className="single__counter">
                            <span className="counter">
                                <CountUp start={0} end={12} duration={2}
                                suffix="K" />
                            </span>

                            <p className='counter__title'>
                                Medical (Monthly)
                            </p>
                        </div>
                        </div>


                        
                    </div>
              </div>
            </Col>
        </Row>
    </Container>
  </section>
}

export default AboutUS