

import React from 'react'
import { Container, Row, Col } from 'reactstrap'
import './company-section.css'
const Company = () => {
    return <section>
        <Row >
            <Container></Container>
            <Col lg='2' md='3' className='logoo1'>
                <h3 className='d-flex align-items-center gap-1'>
                    <i class="ri-facebook-circle-fill"></i>FACEBOOK
                </h3>
            </Col>
            <Col lg='2' md='3' className='logoo2'>
                <h3 className='d-flex align-items-center gap-1'>
                    <i class="ri-instagram-fill"></i>INSTAGRAM
                </h3>
            </Col>

            <Col lg='2' md='3' className='logoo6'>
                <h3 className='d-flex align-items-center gap-1'>
                <i class="ri-whatsapp-fill"></i>WHATSAPP
                </h3>
            </Col>
            <Col lg='2' md='3' className='logoo3'>
                <h3 className='d-flex align-items-center gap-1'>
                    <i class="ri-twitter-fill"></i>TWITTER
                </h3>
            </Col>
            
           
            <Col lg='2' md='3' className='logoo4'>
                <h3 className='d-flex align-items-center gap-1'>
                <i class="ri-telegram-fill"></i>TELEGRAM
                </h3>
            </Col>

            <Col lg='2' md='3' className='logoo5'>
                <h3 className='d-flex align-items-center gap-1'>
                    <i class="ri-google-fill"></i>GOOGLE
                </h3>
            </Col>
        </Row>
    </section>


};

export default Company;